html,
body {
    height: 100%;
    width: 100%;
}

body {
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    color: white;
}

body.fontLoaded {
    font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

#root {
    background-color:#1565C0;
    min-height: 100%;
    min-width: 100%;
}

p,
label {
    font-family: Georgia, Times, 'Times New Roman', serif;
    line-height: 1.5em;
}